@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');

/* CSS RESET */
/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input, button, textarea, select {
  font: inherit;
}

/* 8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

/* APP */

body {
    background-color: #001413;
    background-image: url("https://www.transparenttextures.com/patterns/broken-noise.png");
    color: white;
    font-family: "Rubik", sans-serif;
    /* font-optical-sizing: auto;
    font-weight: <weight>;
    font-style: normal; */
}

/* container */

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem; /* added this myself */
}

/* todos list */

.title {
    text-align: center;
    font-size: 3rem;
    padding: 1rem 0.1rem;
}

.todos {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.todo {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 1rem;

    background-color: rgba(255,255,255,0.1);
    width: 50%;
    font-size: 1.25rem;
    border-radius: 1rem;
    transition: all 0.2s;

    &:hover{
        background-color: rgba(255,255,255,0.2);
    }
}

.mutations {
    display: flex;
    gap: 0.75rem;
}

.todo__status,
.todo__delete {
    border: none;
    padding: 0;
    line-height: 1;
    cursor: pointer;
    transition: all 0.2s;
    background: none;
    color: white; 
    font-size: 1.6rem;

    &:hover {
        transform: scale(1.1);
    }
}

.todo__status {
    font-size: 2rem;
}


/* form */
.form {
    display: flex;
    gap: 0.5rem;
    width: 50%;
    margin: 1rem auto;
}

.form__input {
    flex: 1;
    padding: 0.5rem 0.75rem;
    border-radius: 0.25rem;
    border: none;
    outline: none;
    /* transition: all 2s; */

    &:focus,
    &:hover {
        background-color: rgba(255,255,255,0.9);
    }
}

.form__button {
    border: none;
    background-color: rgba(255,255,255,0.55);
    border: none;
    cursor: pointer;
    color: white;
    border-radius: 0.25rem;
    transition: all 0.2s;
    
    /* On hover changes to what is listed inside */
    &:hover{ 
        /* background-color: #001413; This is main background color*/
        background-color: rgba(255,255,255,0.4);
    }
}

@media (max-width: 430px) {
    .title {
        font-size: 1.5rem;
    }

    .form {
        width: 95%;
    }

    .todo {
        width: 95%;
        font-size: 1.1rem;
    }
}
